// @flow

import { getBaseUrl } from '../../utils/environment'
import { List } from 'immutable'
import { PSPDFKitError } from '@pspdfkit/shared'
import StandaloneBackend from './StandaloneBackend'
import type { StandaloneConfiguration } from '../../Configuration'
import { getElectronAppId } from '../../core/electron'
import { Options } from '../../constants/config'

let previousLicenseKey

export {
  corePool,
  validateStandaloneConfiguration,
  normalizeCoreOptions,
} from './StandaloneBackend'

export default class StandaloneBackendWASM extends StandaloneBackend {
  constructor(configuration: StandaloneConfiguration) {
    const baseUrl = configuration.baseUrl || getBaseUrl(window.document)
    const configWithBaseUrl = { ...configuration, baseUrl }

    if (typeof configWithBaseUrl.baseUrl !== 'string') {
      throw new PSPDFKitError(
        '`baseUrl` is mandatory and must be a valid URL, e.g. `https://example.com/`',
      )
    }

    // Validate pdf
    if (
      typeof configWithBaseUrl.pdf !== 'string' &&
      !(configWithBaseUrl.pdf instanceof ArrayBuffer)
    ) {
      throw new PSPDFKitError(
        'pdf must be either an URL to a pdf, e.g. `https://example.com/document.pdf`, or an `ArrayBuffer`',
      )
    }

    // Validate licenseKey
    if (typeof configWithBaseUrl.licenseKey !== 'string') {
      throw new PSPDFKitError(
        'licenseKey must be set to a string value. Please obtain yours from https://customers.pspdfkit.com.',
      )
    } else if (previousLicenseKey && previousLicenseKey !== configWithBaseUrl.licenseKey) {
      throw new PSPDFKitError(
        `Trying to re-use PSPDFKit for Web with a different licenseKey than the previous one.
Unfortunately we only allow one licenseKey per instance.
Please contact support for further assistance.`,
      )
    } else if (configWithBaseUrl.licenseKey.startsWith('TRIAL-')) {
      throw new PSPDFKitError(`You're using the npm key instead of the license key. This key is used to download the PSPDFKit for Web package via the node package manager.

You can find the license key to unlock your trial by following the link in your trial email and heading to:
https://pspdfkit.com/guides/web/current/standalone/integration/`)
    }

    super(configWithBaseUrl)
  }

  async load(password: ?string) {
    let documentBufferPromise
    if (typeof this._state.pdf === 'string') {
      documentBufferPromise = fetch(this._state.pdf, { credentials: 'same-origin' }).then(d =>
        d.arrayBuffer(),
      )
    } else {
      documentBufferPromise = this._state.pdf
    }

    const { features } = await this.client.load(this._state.baseUrl, this._state.licenseKey, {
      mainThreadOrigin: getElectronAppId() || window.location.origin,
      disableWebAssembly: this._state.disableWebAssembly,
      disableWebAssemblyStreaming: this._state.disableWebAssemblyStreaming,
      disableIndexedDBCaching: this._state.disableIndexedDBCaching,
      enableAutomaticLinkExtraction: this._state.enableAutomaticLinkExtraction,
      overrideMemoryLimit: this._state.overrideMemoryLimit,
    })
    this._state = this._state.set('features', List(features))
    previousLicenseKey = this._state.licenseKey
    const documentBuffer = await documentBufferPromise

    let documentResponse
    try {
      documentResponse = await this.client.openDocument(documentBuffer, password)
    } catch (error) {
      // When we get an INVALID_PASSWORD error, the PDF is transferred back to the main thread
      // and we need to save it again in the state.
      if (error.message === 'INVALID_PASSWORD' && this._state.pdf instanceof ArrayBuffer) {
        this._state = this._state.set('pdf', error.callArgs[0])
      }
      throw error
    }

    if (this._XFDF) {
      await this.client.importXFDF(this._XFDF.source, this._XFDF.keepCurrentAnnotations)
    }

    let jsActionChanges = []
    if (Options.PDF_JAVASCRIPT) {
      jsActionChanges = await this.client.enablePDFJavaScriptSupport()
    }

    // If the backend was instantiated with an instant JSON, we need to verify that the
    // pdfId if present
    if (this._instantJSON && this._instantJSON.pdfId && documentResponse.ID.permanent) {
      const expectedIds = this._instantJSON.pdfId
      const actualIds = documentResponse.ID

      if (expectedIds.permanent !== actualIds.permanent) {
        throw new PSPDFKitError(
          `Could not instantiate from Instant JSON: Permanent PDF ID mismatch.
Please use the same PDF document that was used to create this Instant JSON.
For more information, please visit: https://pspdfkit.com/guides/web/current/importing-exporting/instant-json/`,
        )
      }

      if (expectedIds.changing !== actualIds.changing) {
        throw new PSPDFKitError(
          `Could not instantiate from Instant JSON: Changing PDF ID mismatch.
Please use the same revision of this PDF document that was used to create this Instant JSON.
For more information, please visit: https://pspdfkit.com/guides/web/current/importing-exporting/instant-json/`,
        )
      }
    }

    // We should then set them in the StandaloneBackendState and return them in this method as a list.
    this._state = this._state.set('documentResponse', documentResponse)

    return {
      features: this._state.features,
      hasPassword: !!password,
      allowedTileScales: 'all',
      jsActionChanges,
    }
  }
}
