/* @flow */

import CoreWorker from './Core.worker.js'

import type {
  Core,
  DocumentResponse,
  LoadResponse,
  MessageId,
  Request,
  Response,
  SearchResponse,
  TextResponse,
} from './CoreTypes'
import { Map } from 'immutable'
import { PSPDFKitError } from '@pspdfkit/shared'
import { invariant } from '@pspdfkit/shared'
import type { ActionChange } from '../models/actions/Action'
import type { BookmarkProps } from '../models/Bookmark'
import type { OutlineElementProps } from '../models/OutlineElement'
import type { JavaScriptEventElement } from '../models/javascript/JavaScriptEventElement'
import type { Recyclable } from './ObjectPool'
import type { DocumentOperationJSON } from '../models/DocumentOperation'
import type { FormFieldWithWidgets } from '../models/FormFieldWithWidgets.js'

type PromisePair = ExactWorkaround<{
  resolve: () => any,
  reject: () => any,
}>

const noop = (): void => {}

/**
 * The client is responsible for opening a web worker and pass results of the API back and forth.
 */
export default class CoreClient implements Core, Recyclable {
  _worker: Worker = new CoreWorker()
  _requests: Map<MessageId, PromisePair> = Map()
  _nextRequestId: MessageId = 1
  _isLoading: boolean = true
  _loadPromise: ?Promise<LoadResponse> = null
  _hasOpenedDocument: boolean = false

  constructor() {
    this._worker.onmessage = this._handleMessage
  }

  load(
    baseUrl: string,
    licenseKey: string,
    {
      mainThreadOrigin,
      disableWebAssembly,
      disableWebAssemblyStreaming,
      disableIndexedDBCaching,
      enableAutomaticLinkExtraction,
      overrideMemoryLimit,
    }: {
      mainThreadOrigin: string,
      disableWebAssembly: boolean,
      disableWebAssemblyStreaming: boolean,
      disableIndexedDBCaching: boolean,
      enableAutomaticLinkExtraction: boolean,
      overrideMemoryLimit: null | number,
    },
  ): Promise<LoadResponse> {
    invariant(
      !this._hasOpenedDocument,
      'cannot invoke `load` while an instance is still in use. Please call `recycle` first.',
    )

    if (this._loadPromise) {
      // Don't load again if we have already done so in the past
      return this._loadPromise
    }

    this._loadPromise = this._sendRequest('load', [
      baseUrl,
      licenseKey,
      {
        mainThreadOrigin,
        disableWebAssembly,
        disableWebAssemblyStreaming,
        disableIndexedDBCaching,
        enableAutomaticLinkExtraction,
        overrideMemoryLimit,
      },
    ])
      .then(response => {
        this._isLoading = false
        return response
      })
      .catch(err => {
        this._isLoading = false
        this._worker.terminate()
        throw err
      })

    return this._loadPromise
  }

  version(): Promise<number> {
    this._assertLoaded()
    return this._sendRequest('version')
  }

  openDocument(document: ArrayBuffer, password: ?string): Promise<DocumentResponse> {
    this._assertLoaded()
    this._hasOpenedDocument = true
    return this._sendRequest('openDocument', [document, password]).catch(error => {
      this._hasOpenedDocument = false
      throw error
    })
  }

  async openDocumentAt(): Promise<DocumentResponse> {
    throw new Error('Should never be called')
  }

  async enablePDFJavaScriptSupport(): Promise<Array<ActionChange>> {
    this._assertLoaded()
    return this._sendRequest('enablePDFJavaScriptSupport')
  }

  getBookmarks(): Promise<Array<BookmarkProps>> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('getBookmarks')
  }

  getFormJSON(): Promise<Object> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('getFormJSON')
  }

  evalFormValuesActions(
    newValues: Array<{}>,
    skippedPdfObjectIds: Array<number> = [],
    annotations: Array<Object> = [],
    formFieldValues: Array<Object> = [],
    attachments: { [string]: Blob } = {},
    formFields: Array<Object> = [],
    skippedPdfFormFieldIds: Array<number> = [],
    bookmarks: Array<Object> = [],
    skippedPdfBookmarkIds: Array<string> = [],
  ) {
    this._assertLoaded()
    this._assertDocumentOpen()

    return this._sendRequest('evalFormValuesActions', [
      newValues,
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      formFields,
      attachments,
      bookmarks,
      skippedPdfBookmarkIds,
      skippedPdfFormFieldIds,
    ])
  }

  evalScript(
    source: string,
    formFieldName: ?string,
    skippedPdfObjectIds: Array<number> = [],
    annotations: Array<Object> = [],
    formFieldValues: Array<Object> = [],
    attachments: { [string]: Blob } = {},
    bookmarks: Array<Object> = [],
    skippedPdfBookmarkIds: Array<string> = [],
    formFields: Array<Object> = [],
    skippedPdfFormFieldIds: Array<number> = [],
  ) {
    this._assertLoaded()
    this._assertDocumentOpen()

    return this._sendRequest('evalScript', [
      source,
      formFieldName,
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      attachments,
      bookmarks,
      skippedPdfBookmarkIds,
      formFields,
      skippedPdfFormFieldIds,
    ])
  }

  getFormValues(): Promise<Array<Object>> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('getFormValues')
  }

  closeDocument() {
    this._assertLoaded()
    this._hasOpenedDocument = false
    return this._sendRequest('closeDocument')
  }

  renderTile(
    pageIndex: number,
    pageSize: {| width: number, height: number |},
    rect: {| left: number, top: number, width: number, height: number |},
    renderForPrinting: boolean,
    annotations: ?Array<Object>,
    formFieldValues: ?Array<Object>,
    attachments: ?{ [string]: Blob },
    formFields: ?Array<Object>,
  ): Promise<ArrayBuffer> {
    this._assertLoaded()
    this._assertDocumentOpen()

    return this._sendRequest('renderTile', [
      pageIndex,
      pageSize,
      rect,
      renderForPrinting,
      annotations,
      formFieldValues,
      attachments,
      formFields,
    ])
  }

  renderAnnotation(
    annotation: Object,
    attachment: ?Blob,
    width: number,
    height: number,
  ): Promise<ArrayBuffer> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('renderAnnotation', [annotation, attachment, width, height])
  }

  renderDetachedAnnotation(
    annotation: Object,
    attachment: ?Blob,
    width: number,
    height: number,
  ): Promise<ArrayBuffer> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('renderDetachedAnnotation', [annotation, attachment, width, height])
  }

  onKeystrokeEvent(event: JavaScriptEventElement): Promise<Array<ActionChange>> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('onKeystrokeEvent', [event])
  }

  getAttachment(attachmentId: string): Promise<[ArrayBuffer, string]> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('getAttachment', [attachmentId])
  }

  textForPageIndex(pageIndex: number): Promise<TextResponse> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('textForPageIndex', [pageIndex])
  }

  annotationsForPageIndex(pageIndex: number): Promise<Array<Object>> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('annotationsForPageIndex', [pageIndex])
  }

  createAnnotation(annotation: Object): Promise<number> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('createAnnotation', [annotation])
  }

  updateAnnotation(annotation: Object): Promise<number> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('updateAnnotation', [annotation])
  }

  deleteAnnotation(annotationId: number): Promise<void> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('deleteAnnotation', [annotationId])
  }

  getTextFromRects(
    pageIndex: number,
    rects: Array<{| left: number, top: number, width: number, height: number |}>,
  ): Promise<string> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('getTextFromRects', [pageIndex, rects])
  }

  search(searchTerms: string, startPageIndex: number, limitPages: number): Promise<SearchResponse> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('search', [searchTerms, startPageIndex, limitPages])
  }

  exportFile(
    flatten: boolean = false,
    incremental: boolean = false,
    skippedPdfObjectIds: Array<number> = [],
    annotations: Array<Object> = [],
    formFieldValues: Array<Object> = [],
    attachments: { [string]: Blob } = {},
    formFields: Array<Object> = [],
    skippedPdfFormFieldIds: Array<number> = [],
    bookmarks: Array<Object> = [],
    skippedPdfBookmarkIds: Array<string> = [],
  ): Promise<ArrayBuffer> {
    this._assertLoaded()
    this._assertDocumentOpen()

    return this._sendRequest('exportFile', [
      flatten,
      incremental,
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      attachments,
      formFields,
      skippedPdfFormFieldIds,
      bookmarks,
      skippedPdfBookmarkIds,
    ])
  }

  importXFDF(source: string, keepCurrentAnnotations: boolean = false): Promise<void> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('importXFDF', [source, keepCurrentAnnotations])
  }

  exportXFDF(
    skippedPdfObjectIds: Array<number> = [],
    annotations: Array<Object> = [],
    formFieldValues: Array<Object> = [],
    attachments: { [string]: Blob } = {},
    formFields: Array<Object> = [],
    skippedPdfFormFieldIds: Array<number> = [],
  ): Promise<string> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('exportXFDF', [
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      attachments,
      formFields,
      skippedPdfFormFieldIds,
    ])
  }

  exportInstantJSON(
    skippedPdfObjectIds: Array<number> = [],
    annotations: Array<Object> = [],
    formFieldValues: Array<Object> = [],
    attachments: { [string]: Blob } = {},
    formFields: Array<Object> = [],
    skippedPdfFormFieldIds: Array<number> = [],
    bookmarks: Array<Object> = [],
    skippedPdfBookmarkIds: Array<string> = [],
  ) {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('exportInstantJSON', [
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      attachments,
      formFields,
      skippedPdfFormFieldIds,
      bookmarks,
      skippedPdfBookmarkIds,
    ])
  }

  getDocumentOutline(): Promise<Array<OutlineElementProps>> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('getDocumentOutline')
  }

  applyOperations(
    operations: Array<DocumentOperationJSON>,
    operationsDocuments: { [string]: Blob },
    skippedPdfObjectIds: Array<number> = [],
    annotations: Array<Object> = [],
    formFieldValues: Array<Object> = [],
    attachments: { [string]: Blob } = {},
    bookmarks: Array<Object> = [],
    skippedPdfBookmarkIds: Array<string> = [],
    formFields: Array<Object> = [],
    skippedPdfFormFieldIds: Array<number> = [],
  ): Promise<Array<Object>> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('applyOperations', [
      operations,
      operationsDocuments,
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      attachments,
      bookmarks,
      skippedPdfBookmarkIds,
      formFields,
      skippedPdfFormFieldIds,
    ])
  }

  reloadDocument(): Promise<DocumentResponse> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('reloadDocument')
  }

  exportPDFWithOperations(
    operations: Array<DocumentOperationJSON>,
    operationsDocuments: { [string]: Blob },
    skippedPdfObjectIds: Array<number> = [],
    annotations: Array<Object> = [],
    formFieldValues: Array<Object> = [],
    attachments: { [string]: Blob } = {},
    bookmarks: Array<Object> = [],
    skippedPdfBookmarkIds: Array<string> = [],
    formFields: Array<Object> = [],
    skippedPdfFormFieldIds: Array<number> = [],
  ): Promise<ArrayBuffer> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('exportPDFWithOperations', [
      operations,
      operationsDocuments,
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      attachments,
      bookmarks,
      skippedPdfBookmarkIds,
      formFields,
      skippedPdfFormFieldIds,
    ])
  }

  readFormJSONObjects(): Promise<Array<FormFieldWithWidgets>> {
    this._assertLoaded()
    this._assertDocumentOpen()
    return this._sendRequest('readFormJSONObjects')
  }

  recycle(): void {
    if (this._hasOpenedDocument) {
      this.closeDocument()
    }
    if (!this._isLoading) {
      this._requests = this._requests.map(() => ({
        resolve: noop,
        reject: noop,
      }))
    }
  }

  destroy(): void {
    this._loadPromise = null
    this._worker.terminate()
  }

  /**
   * Throws an error if the native module is not yet initialized.
   */
  _assertLoaded() {
    if (this._isLoading) {
      throw new PSPDFKitError('CoreClient not yet initialized')
    }
  }

  /**
   * Throws an error if trying to do an operation when there is no open document.
   */
  _assertDocumentOpen() {
    if (!this._hasOpenedDocument) {
      throw new PSPDFKitError(
        'This method can not be called since there is no open document. Have you run PSPDFKit.unload()?',
      )
    }
  }

  /**
   * When we receive messages from the worker, we look up the request ID in our requests map and
   * resolve the promises accordingly.
   *
   * If the id is `0`, we now that we've successfully initialized
   */
  _handleMessage = (event: MessageEvent) => {
    const response: Response = (event.data: any)
    const request = this._requests.get(response.id)
    invariant(request, `No request was made for id ${response.id}.`)
    const { resolve, reject } = request

    this._requests = this._requests.delete(response.id)
    if (response.error) {
      const err = new PSPDFKitError(response.error)
      // $FlowIssue - find a better way to propagate these with the Error.
      err.callArgs = response.callArgs
      reject(err)
    } else {
      // $FlowIssue: Flow should detect that response must have a result at that point.
      resolve(response.result)
    }
  }

  /**
   * Creates a `Request` and sends it to the web worker.
   */
  _sendRequest(action: $PropertyType<Request, 'action'>, args: Array<any> = []): Promise<any> {
    return new Promise((resolve, reject) => {
      const id = this._assignId()

      const request: Request = { id, action, args }

      // When the arg list contains a transferable object, we pass it's ownership as well
      const transferList = args.filter(a => a instanceof ArrayBuffer)

      this._worker.postMessage(request, transferList)

      this._requests = this._requests.set(id, { resolve, reject })
    })
  }

  /**
   * Returns a new, unique identifier.
   */
  _assignId(): MessageId {
    const id = this._nextRequestId
    this._nextRequestId = this._nextRequestId + 1
    return id
  }
}
