/* @flow */

export interface Recyclable {
  // $FlowIssue
  constructor(): *;
  destroy(): void;
  recycle(): void;
}

/**
 * Object pool that allows for reuse of already initialized objects.
 */
export default class ObjectPool<T, U: Recyclable & T> {
  // The maximum number of objects that can be pooled
  size: number = 1

  // List of objects that have been recycled, making them available for reuse.
  //
  // While this is initially an empty list, this is populated once objects have
  // been recycled. The length will not exceed `this.size`.
  _freeObjects: Array<U> = []

  // Used when creating a new object.
  _constructor: Class<U>

  constructor(Constructor: Class<U>) {
    this._constructor = Constructor
  }

  /**
   * Obtain an object instance.
   *
   * If a spare object is available for reuse, this will be returned instead of
   * a newly created one.
   *
   * @returns {{ object: T, checkIn(): void }} An object containing `object` and
   * `checkIn`. You may invoke `checkIn` when you no longer need `object`, at
   * which point the object will become a candidate for reuse if the list of
   * free objects does not exceed the pool size.
   */
  checkOut(): { object: T, checkIn(): void } {
    let object: U
    if (this._freeObjects.length > 0) {
      // Use the first available recycled object if we have one, and remove it
      // from the list
      object = this._freeObjects.shift()
    } else {
      object = new this._constructor()
    }

    return {
      object,
      checkIn: () => {
        // We defer the object to GC for cleanup when the pool is full.
        if (this._freeObjects.length >= this.size) {
          object.destroy()
          return
        }

        object.recycle()
        this._freeObjects.push(object)
      },
    }
  }
}
