// @flow

import { Backend } from '../Backend'
import { bufferToImageHandle } from '../../utils/dom'
import { fromJSON as documentOutlineFromJSON } from '../serializers/OutlineElementSerializer'
import { invariant } from '@pspdfkit/shared'
import { isModifiableAnnotation } from '../../utils/annotation'
import { jsonToTextLines } from '../../utils/textLines'
import { List } from 'immutable'
import { MAX_RESOURCE_REQUESTS_IN_QUEUE_STANDALONE } from '../../constants/config'
import { parseJsonSearchResults } from '../../utils/search'
import { PSPDFKitError } from '@pspdfkit/shared'
import {
  serializeAnnotation,
  serializeFormFieldValue,
  serializeFormField,
} from '../serializers/utils'
import { StandaloneProvider } from '../StandaloneProvider/StandaloneProvider'
import Attachment from '../../models/Attachment'
import CallbackRequest from '../requests/types/CallbackRequest'
import generateUlid from '../../utils/ulid'
import ImageHandle from '../handles/ImageHandle'
import ObjectPool from '../../core/ObjectPool'
import RequestQueue from '../requests/RequestQueue'
import StandaloneBackendState from './StandaloneBackendState'
import type { Annotation, FormFieldValue, BackendPermissions } from '../../models'
import type { ActionChange } from '../../models/actions/Action'
import type { AnnotationProvider } from '../AnnotationProvider'
import type { BookmarkProvider } from '../BookmarkProvider'
import type { Core } from '../../core/CoreTypes'
import type { DocumentResponse } from '../../core/CoreTypes'
import type { FormFieldValueProvider } from '../FormFieldValueProvider'
import type { InstantJSON } from '../InstantJSON'
import type { Map } from 'immutable'
import type { Size, Rect } from '../../models/geometry'
import type { StandaloneConfiguration } from '../../Configuration'
import type { TextResponse, FormResponse, BackendType, LoadResponse } from '../Backend'
import type { DocumentOperationJSON } from '../../models/DocumentOperation'
import type OutlineElement from '../../models/OutlineElement'
import type SearchResult from '../../models/search/SearchResult'
import type { JavaScriptEventElement } from '../../models/javascript/JavaScriptEventElement'
import type FormField from '../../models/form-fields/FormField'

let CoreClass
if (PSPDFKIT_DEFINES.STANDALONE_NATIVE) {
  CoreClass = require('../../core/CoreNative').default
} else if (PSPDFKIT_DEFINES.WASM_IN_MAIN_THREAD) {
  CoreClass = require('../../core/Core').default
} else {
  CoreClass = require('../../core/CoreClient').default
}

// $FlowIssue
export const corePool: ObjectPool<Core, *> = new ObjectPool(CoreClass)

export default class StandaloneBackend implements Backend {
  provider: AnnotationProvider & BookmarkProvider & FormFieldValueProvider
  client: Core
  checkIn: () => void

  type: BackendType = 'STANDALONE'

  _state: StandaloneBackendState
  _requestQueue: RequestQueue
  _instantJSON: ?InstantJSON
  _XFDF: ?{
    source: string,
    keepCurrentAnnotations: boolean,
  } = null

  constructor(configuration: StandaloneConfiguration) {
    validateStandaloneConfiguration(configuration)

    const {
      baseUrl,
      instantJSON,
      XFDF,
      enableAutomaticLinkExtraction,
      overrideMemoryLimit,
    } = configuration

    if (typeof XFDF === 'string') {
      this._XFDF = {
        source: XFDF,
        keepCurrentAnnotations: configuration.XFDFKeepCurrentAnnotations === true,
      }
    }

    // Validate instantJSON
    if (instantJSON && instantJSON.annotations) {
      instantJSON.annotations = instantJSON.annotations.map(annotation => {
        annotation.id = annotation.id.toString()
        return annotation
      })
    }

    this._instantJSON = instantJSON

    // Validate disableWebAssembly
    const {
      disableWebAssembly,
      disableWebAssemblyStreaming,
      disableIndexedDBCaching,
    } = configuration

    // Validate standaloneInstancesPoolSize
    const { standaloneInstancesPoolSize } = configuration
    if (typeof standaloneInstancesPoolSize !== 'undefined') {
      corePool.size = standaloneInstancesPoolSize
    }

    this._state = new StandaloneBackendState(
      // $FlowIssue: We are handling null/undefined in the following call
      normalizeCoreOptions({
        baseUrl,
        licenseKey: configuration.licenseKey,
        pdf: configuration.pdf,
        disableWebAssembly,
        disableWebAssemblyStreaming,
        disableIndexedDBCaching,
        enableAutomaticLinkExtraction,
        overrideMemoryLimit,
        documentHandle: '0',
      }),
    )

    this._requestQueue = new RequestQueue(MAX_RESOURCE_REQUESTS_IN_QUEUE_STANDALONE)
    const { object, checkIn } = corePool.checkOut()
    this.client = object
    this.checkIn = checkIn

    const providerJSON = instantJSON
      ? {
          annotations: instantJSON.annotations || [],
          formFields: instantJSON.formFields || [],
          formFieldValues: instantJSON.formFieldValues || [],
          skippedPdfObjectIds: instantJSON.skippedPdfObjectIds || [],
          skippedPdfFormFieldIds: instantJSON.skippedPdfFormFieldIds || [],
          attachments: instantJSON.attachments || {},
          bookmarks: instantJSON.bookmarks || [],
          skippedPdfBookmarkIds: instantJSON.skippedPdfBookmarkIds || [],
        }
      : null

    this.provider = new StandaloneProvider(this.client, providerJSON)
  }

  isUsingInstantProvider(): boolean {
    return false
  }

  async load(password: ?string): Promise<LoadResponse> {
    return {
      features: this._state.features,
      hasPassword: !!password,
      allowedTileScales: 'all',
      jsActionChanges: [],
    }
  }

  destroy(): void {
    this.provider && this.provider.destroy()
    this._requestQueue && this._requestQueue.destroy()
    this.checkIn()
  }

  async documentInfo(): Promise<DocumentResponse> {
    return this._state.documentResponse
  }

  getDocumentHandle(): string {
    return this._state.documentHandle
  }

  getFormJSON(): Promise<FormResponse> {
    return this.client.getFormJSON()
  }

  permissions(): Promise<BackendPermissions> {
    return Promise.resolve(this._state.backendPermissions)
  }

  textForPageIndex(pageIndex: number): {| promise: Promise<TextResponse>, cancel: Function |} {
    const identifier = `${pageIndex}-text`
    let isCanceled = false

    const request = new CallbackRequest(identifier, () => {
      if (isCanceled) {
        return Promise.reject({ aborted: true })
      }

      return this.client.textForPageIndex(pageIndex).then(json => {
        if (isCanceled) {
          // When the request has been cancelled we return a rejected Promise like aborted XHR do.
          return Promise.reject({ aborted: true })
        }
        return jsonToTextLines({ textLines: json }, pageIndex)
      })
    })

    const { promise, cancel } = this._requestQueue.enqueue(request)

    return {
      promise,
      cancel: () => {
        isCanceled = true
        cancel()
      },
    }
  }

  getTextFromRects(pageIndex: number, rects: List<Rect>): Promise<string> {
    return this.client.getTextFromRects(pageIndex, (rects.toJS(): any))
  }

  renderTile(
    pageIndex: number,
    pageSize: Size,
    tileRect: Rect,
    renderForPrinting: boolean,
    contentState: ?{
      annotations: List<Annotation>,
      formFieldValues: List<FormFieldValue>,
      attachments: Map<string, Attachment>,
      formFields: List<FormField>,
    },
  ): {| promise: Promise<ImageHandle>, cancel: Function |} {
    const identifier = `${pageIndex}-${pageSize.width}-${pageSize.height}-${tileRect.top}-${
      tileRect.left
    }-${tileRect.width}-${tileRect.height}-${this.getDocumentHandle()}`
    const request = new CallbackRequest(identifier, () => {
      let annotationsToRender = contentState
        ? (contentState.annotations
            .filter(isModifiableAnnotation)
            .map(serializeAnnotation)
            .toJS(): any)
        : null

      let formFieldsToRender = contentState
        ? (contentState.formFields.map(serializeFormField).toJS(): any)
        : null

      let formFieldValuesToRender = contentState
        ? (contentState.formFieldValues.map(serializeFormFieldValue).toJS(): any)
        : null

      const attachments = contentState
        ? (contentState.attachments.map(attachment => attachment.data).toJS(): any)
        : null

      return this.client
        .renderTile(
          pageIndex,
          pageSize.toObject(),
          tileRect.toObject(),
          renderForPrinting,
          annotationsToRender,
          formFieldValuesToRender,
          attachments,
          formFieldsToRender,
        )
        .then(buffer =>
          bufferToImageHandle({ buffer, width: tileRect.width, height: tileRect.height }),
        )
    })
    const hasPriority = pageSize.width === tileRect.width && pageSize.height === tileRect.height
    const { promise, cancel } = this._requestQueue.enqueue(request, hasPriority)

    return { promise, cancel }
  }

  renderAnnotation(
    annotation: Annotation,
    attachment: ?Attachment,
    width: number,
    height: number,
  ): {| promise: Promise<ImageHandle>, cancel: Function |} {
    const identifier = annotation.id
    const attachmentData = attachment && attachment.data
    const request = new CallbackRequest(identifier, () => {
      return this.client
        .renderAnnotation(serializeAnnotation(annotation), attachmentData, width, height)
        .then(buffer => bufferToImageHandle({ buffer, width, height }))
    })

    const { promise, cancel } = this._requestQueue.enqueue(request, false)

    return { promise, cancel }
  }

  renderDetachedAnnotation(
    annotation: Annotation,
    attachment: ?Attachment,
    width: number,
    height: number,
  ): {| promise: Promise<ImageHandle>, cancel: Function |} {
    // A detached annotation does not have an id, so we generate one
    // for the queue identifier.
    if (annotation.id) {
      throw new PSPDFKitError(`Detached annotations should not have an \`id\`: ${annotation.id}`)
    }
    const identifier = generateUlid()
    const attachmentData = attachment && attachment.data
    const request = new CallbackRequest(identifier, () => {
      return this.client
        .renderDetachedAnnotation(serializeAnnotation(annotation), attachmentData, width, height)
        .then(buffer => bufferToImageHandle({ buffer, width, height }))
    })

    const { promise, cancel } = this._requestQueue.enqueue(request, false)

    return { promise, cancel }
  }

  async getAttachment(attachmentId: string): Promise<Blob> {
    const [data, type] = await this.client.getAttachment(attachmentId)
    return new Blob([data], { type })
  }

  async search(
    searchTerm: string,
    startPageIndex: number,
    limitPages: number,
  ): Promise<List<SearchResult>> {
    const results = await this.client.search(searchTerm, startPageIndex, limitPages)
    return parseJsonSearchResults(results)
  }

  exportPDF(flatten: boolean = false, incremental: boolean = false): Promise<ArrayBuffer> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )
    const {
      skippedPdfObjectIds,
      annotations,
      skippedPdfBookmarkIds,
      bookmarks,
      formFieldValues,
      formFields,
      skippedPdfFormFieldIds,
      attachments,
    } = this.provider.toJSON()
    return this.client.exportFile(
      flatten,
      incremental,
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      attachments,
      formFields,
      skippedPdfFormFieldIds,
      bookmarks,
      skippedPdfBookmarkIds,
    )
  }

  exportXFDF(): Promise<string> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )
    const {
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      formFields,
      attachments,
      bookmarks,
      skippedPdfFormFieldIds,
    } = this.provider.toJSON()
    return this.client.exportXFDF(
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      formFields,
      attachments,
      skippedPdfFormFieldIds,
      bookmarks,
    )
  }

  exportInstantJSON(): Promise<InstantJSON> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone build requires Standalone annotation provider.',
    )
    const {
      skippedPdfObjectIds,
      skippedPdfBookmarkIds,
      annotations,
      formFieldValues,
      formFields,
      attachments,
      bookmarks,
      skippedPdfFormFieldIds,
    } = this.provider.toJSON()
    return this.client.exportInstantJSON(
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      attachments,
      formFields,
      skippedPdfFormFieldIds,
      bookmarks,
      skippedPdfBookmarkIds,
    )
  }

  getPDFURL(): {| promise: Promise<string>, revoke: Function |} {
    return this.generatePDFObjectURL()
  }

  generatePDFObjectURL(): {| promise: Promise<string>, revoke: Function |} {
    let objectUrl,
      isRevoked = false

    const promise = new Promise(resolve => {
      this.exportPDF(true).then(arrayBuffer => {
        if (isRevoked) {
          return
        }
        const blob = new Blob([arrayBuffer], { type: 'application/pdf' })
        objectUrl = window.URL.createObjectURL(blob)
        resolve(objectUrl)
      })
    })

    return {
      promise,
      revoke: () => {
        if (objectUrl) {
          window.URL.revokeObjectURL(objectUrl)
        }
        isRevoked = true
      },
    }
  }

  async getDocumentOutline(): Promise<List<OutlineElement>> {
    const outlineJSON = await this.client.getDocumentOutline()
    return List(outlineJSON.map(documentOutlineFromJSON))
  }

  async onKeystrokeEvent(event: JavaScriptEventElement): Promise<Array<ActionChange>> {
    return await this.client.onKeystrokeEvent(event)
  }

  async evalFormValuesActions(newValues: List<FormFieldValue>): Promise<Array<ActionChange>> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    const {
      skippedPdfObjectIds,
      annotations,
      skippedPdfBookmarkIds,
      bookmarks,
      formFieldValues,
      formFields,
      attachments,
      skippedPdfFormFieldIds,
    } = this.provider.toJSON()

    return this.client.evalFormValuesActions(
      newValues.map(serializeFormFieldValue).toJS(),
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      formFields,
      attachments,
      bookmarks,
      skippedPdfBookmarkIds,
      skippedPdfFormFieldIds,
    )
  }

  async evalScript(source: string, formFieldName: ?string): Promise<Array<ActionChange>> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    const {
      skippedPdfObjectIds,
      annotations,
      skippedPdfBookmarkIds,
      bookmarks,
      formFieldValues,
      attachments,
      formFields,
      skippedPdfFormFieldIds,
    } = this.provider.toJSON()

    return this.client.evalScript(
      source,
      formFieldName,
      skippedPdfObjectIds,
      annotations,
      formFieldValues,
      attachments,
      bookmarks,
      skippedPdfBookmarkIds,
      formFields,
      skippedPdfFormFieldIds,
    )
  }

  async applyOperationsAndReload(operations: Array<DocumentOperationJSON>): Promise<LoadResponse> {
    invariant(
      this.provider instanceof StandaloneProvider,
      'Standalone can only use standalone annotation provider',
    )

    const {
      skippedPdfObjectIds,
      annotations,
      skippedPdfBookmarkIds,
      bookmarks,
      formFieldValues,
      skippedPdfFormFieldIds,
      formFields,
      attachments,
    } = this.provider.toJSON()

    try {
      let coreOperations, coreOperationsDocuments
      if (PSPDFKIT_DEFINES.STANDALONE_NATIVE) {
        // We already have the ids/paths in the operations list, so we don't
        // need anything in coreOperationsDocuments
        coreOperations = operations
        coreOperationsDocuments = {}
      } else {
        const { processedOperations, operationsDocuments } = await getProcessedOperations(
          operations,
        )

        coreOperations = processedOperations
        coreOperationsDocuments = operationsDocuments
      }

      // TODO(marco): Update CoreTypes and impls for operationsDocuments when
      // James' PR is merged?

      await this.client.applyOperations(
        coreOperations,
        coreOperationsDocuments,
        skippedPdfObjectIds,
        annotations,
        formFieldValues,
        attachments,
        bookmarks,
        skippedPdfBookmarkIds,
        formFields,
        skippedPdfFormFieldIds,
      )
    } catch (e) {
      throw new PSPDFKitError(`Applying operations failed: ${e}`)
    }

    return this.reloadDocument()
  }

  async reloadDocument(): Promise<LoadResponse> {
    try {
      this.provider = new StandaloneProvider(this.client, null)

      const documentResponse = await this.client.reloadDocument()

      this._state = this._state
        .set('documentResponse', documentResponse)
        .set('documentHandle', (parseInt(this._state.documentHandle) + 1).toString())

      const jsActionChanges = await this.client.enablePDFJavaScriptSupport()

      return {
        features: this._state.features,
        hasPassword: false,
        allowedTileScales: 'all',
        jsActionChanges,
      }
    } catch (e) {
      throw new PSPDFKitError(`Reloading after applying operations failed: ${e}`)
    }
  }

  async exportPDFWithOperations(operations: Array<DocumentOperationJSON>): Promise<ArrayBuffer> {
    try {
      invariant(
        this.provider instanceof StandaloneProvider,
        'Standalone can only use standalone annotation provider',
      )

      const {
        skippedPdfObjectIds,
        annotations,
        skippedPdfBookmarkIds,
        bookmarks,
        formFieldValues,
        formFields,
        attachments,
        skippedPdfFormFieldIds,
      } = this.provider.toJSON()

      const { processedOperations, operationsDocuments } = await getProcessedOperations(operations)

      return this.client.exportPDFWithOperations(
        processedOperations,
        operationsDocuments,
        skippedPdfObjectIds,
        annotations,
        formFieldValues,
        attachments,
        bookmarks,
        skippedPdfBookmarkIds,
        formFields,
        skippedPdfFormFieldIds,
      )
    } catch (e) {
      throw new PSPDFKitError(`Exporting PDF with operations failed: ${e}`)
    }
  }

  // Cancels all current requests and removes all associated promises.
  cancelRequests() {
    this._requestQueue.cancelAll()
  }
}

/**
 * Normalize options passed to StandaloneBackendState, or even those passed
 * directly to CoreClient.
 */
export function normalizeCoreOptions(options: {
  baseUrl: string,
  licenseKey: string,
  pdf: string | ArrayBuffer,
  disableWebAssembly: boolean,
  disableWebAssemblyStreaming: boolean,
  disableIndexedDBCaching: boolean,
  enableAutomaticLinkExtraction: boolean,
  overrideMemoryLimit: number,
  documentHandle?: string,
}) {
  return {
    baseUrl: options.baseUrl,
    licenseKey: options.licenseKey,
    pdf: options.pdf,
    disableWebAssembly: !!options.disableWebAssembly,
    disableWebAssemblyStreaming: !!options.disableWebAssemblyStreaming,
    disableIndexedDBCaching: !!options.disableIndexedDBCaching,
    enableAutomaticLinkExtraction: !!options.enableAutomaticLinkExtraction,
    overrideMemoryLimit:
      typeof options.overrideMemoryLimit === 'number' ? options.overrideMemoryLimit : null,
    documentHandle: typeof options.documentHandle === 'number' ? options.documentHandle : '0',
  }
}

export function validateStandaloneConfiguration(configuration: StandaloneConfiguration) {
  const {
    licenseKey,
    instantJSON,
    XFDF,
    disableWebAssembly,
    disableWebAssemblyStreaming,
    disableIndexedDBCaching,
    enableAutomaticLinkExtraction,
    overrideMemoryLimit,
    standaloneInstancesPoolSize,
    baseUrl,
  } = configuration

  invariant(
    typeof baseUrl === 'string',
    '`baseUrl` is mandatory and must be a valid URL, e.g. `https://example.com/',
  )

  const isAbsoluteUrl = baseUrl.indexOf('://') > 0
  invariant(
    isAbsoluteUrl,
    `The baseUrl must be an absolute URL (e.g. https://example.com/pspdfkit-lib/), but it is set to ${baseUrl}.
Protocol relative URLs (e.g. //example.com/pspdfkit-lib/) are not supported.`,
  )

  const hasTrailingSlash = baseUrl.substr(-1) === '/'
  invariant(
    hasTrailingSlash,
    `The baseUrl must have a trailing slash in the URL (e.g. https://example.com/pspdfkit-lib/), but it is set to ${baseUrl}.`,
  )

  invariant(
    typeof licenseKey === 'string',
    'licenseKey must be set to a string value. Please obtain yours from https://customers.pspdfkit.com.',
  )
  invariant(
    !licenseKey.startsWith('TRIAL-'),
    `You're using the npm key instead of the license key. This key is used to download the PSPDFKit for Web package via the node package manager.

You can find the license key to unlock your trial by following the link in your trial email and heading to:
https://pspdfkit.com/guides/web/current/standalone/integration/`,
  )

  invariant(typeof XFDF === 'undefined' || typeof XFDF === 'string', 'XFDF must be a string')

  if (instantJSON) {
    invariant(
      typeof instantJSON === 'object' && instantJSON !== null,
      'instantJSON must be an Object',
    )
    invariant(typeof XFDF === 'undefined', 'Cannot import from both instantJSON and XFDF')
    invariant(
      instantJSON.format === 'https://pspdfkit.com/instant-json/v1',
      "instantJSON has an invalid format, please use 'https://pspdfkit.com/instant-json/v1",
    )

    const { pdfId } = instantJSON
    if (pdfId) {
      invariant(
        typeof pdfId === 'object' &&
          pdfId !== null &&
          ((typeof pdfId.permanent === 'string' && typeof pdfId.changing === 'string') ||
            (typeof pdfId.permanent !== 'string' && typeof pdfId.changing !== 'string')),
        'instantJSON has an invalid pdfId',
      )
    }

    invariant(
      typeof instantJSON.skippedPdfObjectIds === 'undefined' ||
        Array.isArray(instantJSON.skippedPdfObjectIds),
      'instantJSON has invalid skippedPdfObjectIds',
    )

    invariant(
      typeof instantJSON.annotations === 'undefined' ||
        (Array.isArray(instantJSON.annotations) &&
          instantJSON.annotations.every(anno => typeof anno === 'object' && anno !== null)),
      'instantJSON has invalid annotations',
    )

    invariant(
      typeof instantJSON.formFieldValues === 'undefined' ||
        (Array.isArray(instantJSON.formFieldValues) &&
          instantJSON.formFieldValues.every(val => typeof val === 'object' && val !== null)),
      'instantJSON has invalid form field values',
    )

    invariant(
      typeof instantJSON.skippedPdfBookmarkIds === 'undefined' ||
        Array.isArray(instantJSON.skippedPdfBookmarkIds),
      'instantJSON has invalid skippedPdfBookmarkIds',
    )

    invariant(
      typeof instantJSON.bookmarks === 'undefined' ||
        (Array.isArray(instantJSON.bookmarks) &&
          instantJSON.bookmarks.every(
            bookmark => typeof bookmark === 'object' && bookmark !== null,
          )),
      'instantJSON has invalid bookmarks',
    )
  }

  invariant(
    typeof disableWebAssembly === 'undefined' || typeof disableWebAssembly === 'boolean',
    'disableWebAssembly must be a boolean',
  )

  invariant(
    typeof disableWebAssemblyStreaming === 'undefined' ||
      typeof disableWebAssemblyStreaming === 'boolean',
    'disableWebAssemblyStreaming must be a boolean',
  )

  invariant(
    typeof disableIndexedDBCaching === 'undefined' || typeof disableIndexedDBCaching === 'boolean',
    'disableIndexedDBCaching must be a boolean',
  )

  invariant(
    typeof enableAutomaticLinkExtraction === 'undefined' ||
      typeof enableAutomaticLinkExtraction === 'boolean',
    'enableAutomaticLinkExtraction must be a boolean',
  )

  invariant(
    typeof overrideMemoryLimit === 'undefined' || typeof overrideMemoryLimit === 'number',
    'overrideMemoryLimit must be a number',
  )

  invariant(
    typeof standaloneInstancesPoolSize === 'undefined' ||
      (typeof standaloneInstancesPoolSize === 'number' && standaloneInstancesPoolSize >= 0),
    'standaloneInstancesPoolSize must be a non-negative number',
  )
}

/**
 * Travserse the operations array. If an `importDocument` operation is found, which includes
 * a `document` blob, the blob will be added to the provided `operationsDocuments` object indexed
 * by the operation stringified array index and the `document` field will be set to that same value.
 *
 * Returns the processed operations array and can modify the provided `operationsDocuments` as well.
 */
async function getProcessedOperations(operations: Array<DocumentOperationJSON>) {
  // If multiple operations use the same blob, we only want to send it once.
  // We track blobs that have already been appended in a WeakMap(blob => index)
  let blobWeakMap = new WeakMap()

  let operationsDocuments = {}
  const processedOperations = await Promise.all(
    operations.map(
      async (operation, index): Promise<DocumentOperationJSON> => {
        if (operation.type === 'importDocument') {
          const document = operation.document
          invariant(
            document instanceof File || document instanceof Blob,
            'Wrong `importDocument` operation `document` value: it must be a File or a Blob',
          )
          let documentIndex
          // Has this blob been already appended?
          if (blobWeakMap.has(document)) {
            // Get the index of the blob
            documentIndex = blobWeakMap.get(document)
          } else {
            documentIndex = index.toString()
            // Add the blob to the payload
            operationsDocuments[documentIndex] = operation.document
            // Track the blob
            blobWeakMap.set(document, documentIndex)
          }
          return {
            ...operation,
            document: documentIndex,
          }
        }
        return operation
      },
    ),
  )
  return {
    processedOperations,
    operationsDocuments,
  }
}
